import React from 'react'
import { NavLink } from 'react-router-dom'
import s from './NotFound.module.sass'

export const NotFound = () => (
    <div className={s.container}>
        { 
            Math.random() > 0.5 ?
            <h1>
                ..    . 
                    "<NavLink to="/"></NavLink>",
                  "<NavLink to="/promo"></NavLink>"?
            </h1>
            :
            <h1>
                  - .  .
                   : "<NavLink to="/"></NavLink>",
                   : "<NavLink to="/promo"></NavLink>".
            </h1>
        }
    </div>
)